import { FC, MouseEvent, useContext, useEffect, useRef } from 'react';
import styled, { css } from 'styled-components';

import {
  Avatar,
  Button,
  DateTimeDisplay,
  defaultThemeProp,
  Flex,
  Icon,
  MenuButton,
  MetaList,
  ModalMethods,
  StyledSecondary,
  SummaryItem,
  Text,
  Tooltip,
  useConfiguration,
  useElement,
  useI18n,
  useModalManager
} from '@pega/cosmos-react-core';
import BareButton from '@pega/cosmos-react-core/lib/components/Button/BareButton';
import { parseToDate } from '@pega/cosmos-react-core/lib/components/DateTime/Input/utils';
import { formatDateTime } from '@pega/cosmos-react-core/lib/components/DateTime/utils';
import { formatListToLocaleString } from '@pega/cosmos-react-core/lib/utils';

import { FeedContext } from './Feed.context';
import FeedModalList from './FeedModalList';
import FeedButton from './FeedButton';
import { FeedContentHeaderProps, FeedModalListProps } from './Feed.types';

const StyledFeedContentHeader = styled.div(({ theme }) => {
  return css`
    margin-block-end: ${theme.base.spacing};
    column-gap: ${theme.base.spacing};

    ${StyledSecondary} {
      ${FeedButton} {
        /* Negative margin needed to align the icon with the author name on the line above. */
        margin-inline-start: calc(-0.75 * ${theme.base.spacing});
      }
    }
  `;
});

StyledFeedContentHeader.defaultProps = defaultThemeProp;

const FeedContentHeader: FC<FeedContentHeaderProps> = (props: FeedContentHeaderProps) => {
  const {
    variant,
    id,
    fullname,
    username,
    postTimestamp,
    content,
    avatarSrc,
    icon,
    userStatus,
    postType,
    postContext,
    recipients = [],
    recipientsCount = 0,
    menuItems,
    edited,
    editTimestamp,
    recipientsLoading,
    onEditSubmit,
    onLoadMoreRecipients,
    onRecipientInteraction,
    onUserClick,
    setEditMode
  } = props;

  const { userInfo: loggedInUser } = useContext(FeedContext);
  const t = useI18n();
  const { locale } = useConfiguration();
  const { create } = useModalManager();

  const recipientsModalRef = useRef<ModalMethods<FeedModalListProps>>();
  const [postTimestampEl, setPostTimestampEl] = useElement<HTMLSpanElement>();
  const [editTimestampEl, setEditTimestampEl] = useElement<HTMLSpanElement>();

  const timestamps = [
    <>
      <DateTimeDisplay ref={setPostTimestampEl} value={postTimestamp} variant='relative' />
      {postTimestamp && (
        <Tooltip target={postTimestampEl}>
          {formatDateTime(parseToDate(postTimestamp), {
            t,
            locale,
            format: 'short',
            variant: 'datetime',
            timeZone: loggedInUser.timeZone
          })}
        </Tooltip>
      )}
    </>
  ];

  if (edited) {
    timestamps.push(
      <>
        <Text ref={setEditTimestampEl} variant='secondary'>
          {t('edited', [<DateTimeDisplay value={editTimestamp} variant='relative' />])}
        </Text>
        {editTimestamp && (
          <Tooltip target={editTimestampEl}>
            {formatDateTime(parseToDate(editTimestamp), {
              t,
              locale,
              format: 'short',
              variant: 'datetime',
              timeZone: loggedInUser.timeZone
            })}
          </Tooltip>
        )}
      </>
    );
  }

  const userVisual = (
    <Avatar
      name={fullname}
      size={variant === 'post' ? 'l' : 'm'}
      imageSrc={avatarSrc}
      icon={icon}
      status={userStatus}
    />
  );

  const recipientsList = recipients.map(x => x.primary);

  useEffect(() => {
    if (recipients.length) {
      recipientsModalRef.current?.update({
        count: recipientsCount,
        heading: t('feed_recipients'),
        listItems: recipients,
        onLoadMore: onLoadMoreRecipients,
        loading: recipientsLoading
      });
    }
  }, [recipientsCount, recipients, onLoadMoreRecipients, recipientsLoading]);

  return (
    <SummaryItem
      as={StyledFeedContentHeader}
      visual={
        onUserClick ? (
          <BareButton
            onClick={(event: MouseEvent) => {
              onUserClick?.({ id, username }, event);
            }}
          >
            {userVisual}
          </BareButton>
        ) : (
          userVisual
        )
      }
      primary={
        <div>
          {onUserClick ? (
            <Button
              variant='text'
              onClick={(event: MouseEvent) => {
                onUserClick?.({ id, username }, event);
              }}
            >
              <Text variant='h3'>{fullname}</Text>
            </Button>
          ) : (
            <Text variant='h3'>{fullname}</Text>
          )}
          {postContext && <Text variant='primary'> {t('feed_context', [postContext])}</Text>}
        </div>
      }
      secondary={
        <Flex container={{ gap: 0.5, alignItems: 'center' }}>
          {postType === 'private' && (
            <Button
              variant='simple'
              as={FeedButton}
              icon
              compact
              onClick={
                recipients.length
                  ? () => {
                      if (recipientsCount > 2) {
                        recipientsModalRef.current = create(FeedModalList, {
                          count: recipientsCount,
                          heading: t('feed_recipients'),
                          listItems: recipients,
                          onLoadMore: onLoadMoreRecipients,
                          loading: recipientsLoading
                        });
                      }
                    }
                  : onRecipientInteraction
              }
              onFocus={onRecipientInteraction}
              onMouseOver={onRecipientInteraction}
              aria-label={`${t('feed_private_post')} ${t(
                'feed_recipients'
              )} ${formatListToLocaleString(recipientsList.slice(0, 2), t, locale, {
                count: recipientsCount
              })}`}
              label={`${t('feed_private_post')}:\n${
                formatListToLocaleString(recipientsList.slice(0, 2), t, locale, {
                  count: recipientsCount,
                  separator: '\n'
                }) || t('feed_private_post')
              }`}
            >
              <Icon name='lock-closed-solid' />
            </Button>
          )}
          <MetaList items={timestamps} />
        </Flex>
      }
      actions={
        menuItems ? (
          <MenuButton
            text={t('feed_post_actions_menu')}
            menu={{
              items:
                onEditSubmit && typeof content === 'string' && username === loggedInUser.username
                  ? [
                      {
                        primary: t('edit'),
                        id: 'edit',
                        onClick: () => setEditMode(true)
                      },
                      ...menuItems
                    ]
                  : menuItems
            }}
            icon='more'
            iconOnly
            variant='simple'
          />
        ) : undefined
      }
    />
  );
};

export default FeedContentHeader;
