import { FC, useContext, useEffect, useRef, useState } from 'react';
import styled, { css } from 'styled-components';

import {
  Button,
  defaultThemeProp,
  Flex,
  ForwardProps,
  Icon,
  useI18n,
  useOuterEvent
} from '@pega/cosmos-react-core';
import { RichTextEditorState } from '@pega/cosmos-react-rte';

import FeedRichText from './FeedRichText';
import { FeedEditRegionProps } from './Feed.types';
import { minInputHeight, StyledActions } from './FeedInputRegion';
import FeedButton from './FeedButton';
import { FeedContext } from './Feed.context';

const StyledInputContainer = styled.div(({ theme }) => {
  return css`
    width: calc(100% - (${theme.base.spacing} + ${theme.base['hit-area']['mouse-min']}));

    @media (pointer: coarse) {
      width: calc(100% - (${theme.base.spacing} + ${theme.base['hit-area']['finger-min']}));
    }
  `;
});

StyledInputContainer.defaultProps = defaultThemeProp;

const FeedEditRegion: FC<ForwardProps & FeedEditRegionProps> = (
  props: ForwardProps & FeedEditRegionProps
) => {
  const { variant, id, content, setEditMode, onEditSubmit, emptyEditText, setEmptyEditText } =
    props;

  const t = useI18n();
  const { setShowSearchResults } = useContext(FeedContext);
  const rteRef = useRef<RichTextEditorState>();
  const editTextAreaRef = useRef<HTMLDivElement>(null);

  const [contentModified, setContentModified] = useState(false);

  useOuterEvent('mousedown', [editTextAreaRef], () => {
    if (emptyEditText) setEditMode(false);
  });

  const handleEdit = () => {
    setShowSearchResults?.(false);
    if (!emptyEditText && contentModified) {
      onEditSubmit?.({
        id,
        value: rteRef.current?.getPlainText() || ''
      });
      setEditMode(false);
    }
  };

  const onKeyDown = (event: KeyboardEvent) => {
    setShowSearchResults?.(true);
    if ((event.metaKey || event.ctrlKey) && event.key === 'Enter') handleEdit();
  };

  useEffect(() => {
    if (rteRef.current) {
      rteRef.current?.focus();
    }
  }, [rteRef]);

  const label = t(variant === 'post' ? 'feed_edit_post' : 'feed_edit_comment');

  return (
    <Flex container={{ gap: 1, alignItems: 'start' }}>
      <StyledInputContainer ref={editTextAreaRef}>
        <FeedRichText
          label={label}
          onChange={() => {
            setEmptyEditText(rteRef.current?.getPlainText().trim() === '');
            setContentModified(rteRef.current?.getPlainText() !== content);
          }}
          onKeyDown={onKeyDown}
          height={{ min: minInputHeight }}
          defaultValue={content}
          ref={rteRef}
        />
      </StyledInputContainer>
      <Flex as={StyledActions} container={{ direction: 'column', gap: 0.25 }}>
        <Button
          variant='simple'
          as={FeedButton}
          onClick={() => setEditMode(false)}
          icon
          label={t('cancel')}
        >
          <Icon name='times' />
        </Button>

        <Button
          as={FeedButton}
          disabled={emptyEditText || !contentModified}
          variant='simple'
          onClick={handleEdit}
          icon
          label={t('update')}
        >
          <Icon name='send' />
        </Button>
      </Flex>
    </Flex>
  );
};

export default FeedEditRegion;
