import { FC, useRef, useEffect } from 'react';
import styled from 'styled-components';

import { registerIcon, defaultThemeProp, ForwardProps, useElement } from '@pega/cosmos-react-core';
import { RichTextEditorState } from '@pega/cosmos-react-rte';
import * as sendIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/send.icon';
import * as paperClipIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/paper-clip.icon';

import { FeedReplyInputProps } from './Feed.types';
import FeedInputRegion from './FeedInputRegion';

registerIcon(sendIcon, paperClipIcon);

export const StyledReplyInputContainer = styled.div``;

StyledReplyInputContainer.defaultProps = defaultThemeProp;

const FeedReplyInput: FC<ForwardProps & FeedReplyInputProps> = (
  props: ForwardProps & FeedReplyInputProps
) => {
  const { id, attachments, onFilesAdded, onSubmit, onSetInputRef, ...restProps } = props;
  const rteRef = useRef<RichTextEditorState>();
  const [newReplyEl, setNewReplyEl] = useElement<HTMLDivElement>();

  useEffect(() => {
    onSetInputRef?.({ ref: rteRef });
  }, [rteRef]);

  return (
    <StyledReplyInputContainer ref={setNewReplyEl} {...restProps}>
      <FeedInputRegion
        variant='reply'
        {...{ id, attachments, onFilesAdded, onSubmit }}
        inputRegionEl={newReplyEl}
        rteRef={rteRef}
      />
    </StyledReplyInputContainer>
  );
};

export default FeedReplyInput;
