import {
  forwardRef,
  FunctionComponent,
  isValidElement,
  PropsWithoutRef,
  ReactElement,
  Ref,
  useContext
} from 'react';
import styled from 'styled-components';

import { Tag, ForwardProps } from '@pega/cosmos-react-core';
import { getXMLAttributes } from '@pega/cosmos-react-rte';

import { FeedContext } from '../Feed/Feed.context';

interface HashtagButtonProps {
  /** Text for the button, or an object containing all necessary props. */
  children: Record<string, string> | ReactElement;
  /** The tag value. */
  tag?: string;
}

const StyledTag = styled(Tag)`
  cursor: pointer;
`;

const serializer = new XMLSerializer();

const HashtagButton: FunctionComponent<HashtagButtonProps & ForwardProps> = forwardRef(
  function HashtagButton(
    { children, tag: tagProp = '', ...restProps }: PropsWithoutRef<HashtagButtonProps>,
    ref: Ref<HTMLButtonElement>
  ) {
    const { onTagClick } = useContext(FeedContext);

    let tag = '';

    if (!isValidElement<any>(children)) {
      if (!children.tag) return <span>{children.rawText}</span>;
      tag = children.tag;
    } else {
      tag = tagProp;
    }

    return (
      <StyledTag {...restProps} ref={ref} onClick={() => onTagClick?.(tag)} spellCheck={false}>
        {isValidElement(children) ? children : tag}
      </StyledTag>
    );
  }
);

export const HashtagButtonConfig = {
  type: 'tag',
  xmlElement: 'pega-tag',
  regexPattern: /(?:^|)<pega-tag (?:[^\n/]|\/(?!>))+\/>/gm,
  component: HashtagButton,
  inject: ({ tag }: { tag: string }) => {
    if (!tag) return;

    const element = document.createElementNS(null, 'pega-tag');
    element.setAttribute('tag', tag);

    return serializer.serializeToString(element);
  },
  extract: (element: string) => {
    const attributes = getXMLAttributes(element);

    if (!attributes || !attributes.tag) return undefined;
    return { tag: attributes.tag };
  }
};

export default HashtagButton;
