import {
  FC,
  forwardRef,
  useState,
  useMemo,
  useRef,
  useEffect,
  PropsWithoutRef,
  useContext
} from 'react';
import styled from 'styled-components';

import { Button, ExpandCollapse, Flex, ForwardProps } from '@pega/cosmos-react-core';
import { RichTextViewer } from '@pega/cosmos-react-rte';
import { StyledEditorContainer } from '@pega/cosmos-react-rte/lib/components/RichTextEditor/RichTextEditor.styles';

import { FeedContext } from './Feed.context';
import { FeedContentProps } from './Feed.types';

const StyledContent = styled.div`
  ${StyledEditorContainer} {
    padding: 0;
  }
`;

const FeedContent: FC<FeedContentProps & ForwardProps> = forwardRef(function FeedContent(
  { children, maxContentHeight = Infinity, ...restProps }: PropsWithoutRef<FeedContentProps>,
  ref: FeedContentProps['ref']
) {
  const { interactionRenderers, markdownMap } = useContext(FeedContext);
  const [collapsed, setCollapsed] = useState(true);
  const [exceedsMaxHeight, setExceedsMaxHeight] = useState(true);
  const rtvRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!rtvRef.current) return;

    const observer = new ResizeObserver(entries => {
      const exceedsHeight = entries.some(({ target, contentRect }) => {
        if (target !== rtvRef.current) return;

        return contentRect.height > maxContentHeight;
      });

      setExceedsMaxHeight(exceedsHeight);
    });

    observer.observe(rtvRef.current);

    return () => {
      observer.disconnect();
    };
  }, [rtvRef.current, maxContentHeight]);

  return useMemo(
    () =>
      typeof children === 'string' ? (
        <Flex as={StyledContent} ref={ref} container={{ direction: 'column', gap: 1 }}>
          <ExpandCollapse
            collapsed={collapsed}
            min={exceedsMaxHeight ? `${maxContentHeight}px` : 'max-content'}
            {...restProps}
          >
            <RichTextViewer
              ref={rtvRef}
              content={children}
              type='markdown'
              interactionRenderers={interactionRenderers}
              markdownMap={markdownMap}
            />
          </ExpandCollapse>
          {exceedsMaxHeight && (
            <Flex container={{ justify: 'center' }}>
              <Button variant='link' onClick={() => setCollapsed(current => !current)}>
                {collapsed ? 'Show more' : 'Show less'}
              </Button>
            </Flex>
          )}
        </Flex>
      ) : (
        children
      ),
    [
      children,
      collapsed,
      exceedsMaxHeight,
      interactionRenderers,
      markdownMap,
      maxContentHeight,
      ref,
      restProps
    ]
  );
});

export default FeedContent;
