import { FC, forwardRef, KeyboardEvent, MouseEvent, Ref } from 'react';
import styled, { css } from 'styled-components';
import { mix } from 'polished';

import {
  defaultThemeProp,
  ForwardProps,
  Tooltip,
  tryCatch,
  useConsolidatedRef,
  useElement,
  useI18n
} from '@pega/cosmos-react-core';

import { useRTEContext } from '../RichTextEditor.context';

import { StyleType } from './Toolbar.types';

export interface ToolBarButtonProps {
  tooltip?: string;
  active?: boolean;
  styleType?: StyleType;
  disabled?: boolean;
  label?: string;
  onMouseDown?: (e: MouseEvent | KeyboardEvent) => void;
}

const StyledToolbarButton = styled.button<ToolBarButtonProps>(props => {
  const { active, theme, styleType } = props;
  const size = `calc(4 * ${props.theme.base.spacing})`;
  const activeColor = tryCatch(() =>
    mix(0.85, theme.base.palette['primary-background'], theme.base.palette.interactive)
  );
  return css`
    background-color: ${active ? activeColor : 'transparent'};
    border: none;
    color: ${active ? theme.base.palette.interactive : theme.base.palette['foreground-color']};
    outline: none;
    display: inline-flex;
    line-height: 1;
    align-items: center;
    justify-content: center;
    height: ${size};
    min-width: ${size};
    opacity: ${active ? 'inherit' : '0.7'};
    cursor: pointer;

    &:focus {
      box-shadow: ${theme.base.shadow.focus};
    }

    &:hover {
      opacity: 1;
    }

    &:disabled {
      cursor: not-allowed;
      box-shadow: none;
    }

    ${styleType === 'BOLD' &&
    css`
      font-weight: bold;
    `}

    ${styleType === 'ITALIC' &&
    css`
      font-style: italic;
    `}

    ${styleType === 'STRIKE-THROUGH' &&
    css`
      text-decoration: line-through;
    `}
  `;
});

StyledToolbarButton.defaultProps = defaultThemeProp;

const ToolbarButton: FC<ToolBarButtonProps & ForwardProps> = forwardRef(function ToolbarButton(
  { tooltip, active, styleType, disabled, label, onMouseDown, ...restProps },
  ref: Ref<HTMLButtonElement>
) {
  const { disabled: contextDisabled } = useRTEContext();
  const [el, setEl] = useElement();
  const toolBarRef = useConsolidatedRef<HTMLButtonElement>(setEl, ref);
  const t = useI18n();
  return (
    <>
      <StyledToolbarButton
        active={active}
        styleType={styleType}
        ref={toolBarRef}
        disabled={contextDisabled || disabled}
        tabIndex={-1}
        onKeyPress={(e: KeyboardEvent<HTMLButtonElement>) => {
          if (e.key === 'Enter') {
            onMouseDown?.(e);
          }
        }}
        onMouseDown={onMouseDown}
        type='button'
        aria-label={`${label}${active ? ` - ${t('active')}` : ''}`}
        aria-pressed={active}
        {...restProps}
      />
      {el && tooltip && (
        <Tooltip target={el} showDelay='none' hideDelay='none'>
          {tooltip}
        </Tooltip>
      )}
    </>
  );
});

export default ToolbarButton;
