import { FC, forwardRef, PropsWithoutRef, useEffect, useContext } from 'react';
import styled, { css } from 'styled-components';

import {
  FileUploadItem,
  Grid,
  Image,
  defaultThemeProp,
  getKindFromMimeType,
  getMimeTypeFromFile,
  Flex,
  usePrevious,
  useI18n
} from '@pega/cosmos-react-core';
import BareButton from '@pega/cosmos-react-core/lib/components/Button/BareButton';

import { FeedContext } from './Feed.context';
import { AttachedFiles, FeedAttachmentsProps } from './Feed.types';

export const StyledFeedAttachments = styled.ul(({ theme }) => {
  return css`
    overflow-x: auto;

    > li {
      background-color: ${theme.base.palette['primary-background']};
      cursor: initial;
    }
  `;
});

StyledFeedAttachments.defaultProps = defaultThemeProp;

export const StyledImagePreview = styled(BareButton)(({ theme }) => {
  return css`
    line-height: 0;
    width: fit-content;
    border-radius: calc(0.5 * ${theme.base['border-radius']});

    & + & {
      margin: 0;
    }

    &:focus {
      box-shadow: ${theme.components.button['focus-shadow']};
    }

    img {
      border-radius: calc(0.5 * ${theme.base['border-radius']});
      object-fit: contain;
      max-height: 10rem;
      max-width: 100%;
    }
  `;
});

StyledImagePreview.defaultProps = defaultThemeProp;

export const StyledAttachmentsRegion = styled.div<FeedAttachmentsProps>(
  ({ variant, readOnly, theme }) => {
    return css`
      margin: ${theme.base.spacing} calc(5 * ${theme.base.spacing}) 0
        calc(${variant === 'post' ? 6 : 5} * ${theme.base.spacing});

      ${readOnly &&
      css`
        margin-block-start: ${theme.base.spacing};
        margin-inline: 0;

        div + ul {
          margin-block-start: ${theme.base.spacing};
        }
      `}
    `;
  }
);

StyledAttachmentsRegion.defaultProps = defaultThemeProp;

const FeedAttachments: FC<FeedAttachmentsProps> = forwardRef(function FeedAttachments(
  props: PropsWithoutRef<FeedAttachmentsProps>,
  ref: FeedAttachmentsProps['ref']
) {
  const { variant, attachments = [], readOnly } = props;
  const t = useI18n();
  const { announce } = useContext(FeedContext);
  const prevAttachments = usePrevious(attachments) ?? [];

  useEffect(() => {
    if (!readOnly && attachments.length !== prevAttachments?.length) {
      const difference = attachments.length - prevAttachments.length;
      const normalizedDifference = difference * (difference > 0 ? 1 : -1);
      const timeoutId = announce(
        t(`feed_attachments_${difference > 0 ? 'added' : 'removed'}`, [normalizedDifference], {
          count: normalizedDifference
        })
      );
      return () => clearTimeout(timeoutId);
    }
  }, [attachments, readOnly]);

  if (attachments.length === 0) return null;

  const previewableFiles: AttachedFiles[] = [];
  const nonPreviewableFiles: AttachedFiles[] = [];

  attachments.forEach(file => {
    const type = getKindFromMimeType(getMimeTypeFromFile(file.name) ?? '');

    if (type === 'image' && file.thumbnail) previewableFiles.push(file);
    else nonPreviewableFiles.push(file);
  });

  return (
    <StyledAttachmentsRegion variant={variant} readOnly={readOnly}>
      {readOnly && !!previewableFiles.length && (
        <Flex container={{ gap: 1, wrap: 'wrap' }}>
          {previewableFiles.map(({ lastModified, name, onPreview, thumbnail }) => {
            return (
              <StyledImagePreview key={lastModified} onClick={() => onPreview?.(name)}>
                <Image alt={name} src={thumbnail} />
              </StyledImagePreview>
            );
          })}
        </Flex>
      )}
      <Grid
        ref={ref}
        as={StyledFeedAttachments}
        container={{
          cols: 'repeat(auto-fill, minmax(15rem, 1fr))',
          gap: 1
        }}
      >
        {!readOnly &&
          previewableFiles.map(({ lastModified, name, size, ...restProps }) => {
            return <FileUploadItem key={lastModified} name={name} size={size} {...restProps} />;
          })}
        {nonPreviewableFiles.map(({ lastModified, name, size, ...restProps }) => {
          return <FileUploadItem key={lastModified} name={name} size={size} {...restProps} />;
        })}
      </Grid>
    </StyledAttachmentsRegion>
  );
});

export default FeedAttachments;
