import {
  forwardRef,
  FunctionComponent,
  isValidElement,
  PropsWithoutRef,
  ReactElement,
  Ref,
  useContext,
  useState,
  MouseEvent
} from 'react';
import styled from 'styled-components';

import {
  Button,
  ForwardProps,
  Link,
  useConsolidatedRef,
  useElement
} from '@pega/cosmos-react-core';
import { getXMLAttributes } from '@pega/cosmos-react-rte';

import { FeedContext } from '../Feed/Feed.context';

interface MentionButtonProps {
  /** Text for the button, or an object containing all necessary props. */
  children: Record<string, string> | ReactElement;
  /** The id of the mention. */
  id?: string;
  /** The text of the mention button. */
  text?: string;
  /** Designates the type of the mention for proper handling on click. */
  type?: string;
  /** If an href is provided, render a link. Otherwise, render a button. */
  href?: string;
}

const StyledButton = styled(Button)`
  &:enabled:focus,
  &:not([disabled]):focus {
    box-shadow: none;
    text-decoration: underline;
  }
`;

const StyledLink = styled(Link)`
  &:enabled:focus,
  &:not([disabled]):focus {
    box-shadow: none;
    text-decoration: underline;
  }
`;

const serializer = new XMLSerializer();

const MentionButton: FunctionComponent<MentionButtonProps & ForwardProps> = forwardRef(
  function MentionButton(
    {
      children,
      id: idProp = '',
      text: textProp = '',
      type: typeProp = '',
      href: hrefProp = '',
      ...restProps
    }: PropsWithoutRef<MentionButtonProps>,
    ref: Ref<HTMLButtonElement>
  ) {
    const { onMentionClick, onMentionPreview } = useContext(FeedContext);
    const [mentionContent, setMentionContent] = useState<JSX.Element | undefined>();
    const [target, setTarget] = useElement<HTMLButtonElement>(null);
    const mentionRef = useConsolidatedRef(ref);

    let id = '';
    let text = '';
    let type = '';
    let href = '';

    if (!isValidElement<any>(children)) {
      if (!children.id || !children.text || !children.type) return <span>{children.rawText}</span>;
      id = children.id;
      text = children.text;
      type = children.type;
      href = children.href || '';
    } else {
      id = idProp;
      text = textProp;
      type = typeProp;
      href = hrefProp;
    }

    const handleClick = (e: MouseEvent<HTMLButtonElement>) => {
      setTarget(e.currentTarget);
      const comp = onMentionClick?.({
        id,
        text,
        type,
        target: e.currentTarget,
        closeGlimpse: () => setTarget(null)
      });
      if (comp) setMentionContent(comp);
    };

    return (
      <>
        {href ? (
          <StyledLink
            {...restProps}
            ref={mentionRef}
            href={href}
            previewable={!!onMentionPreview}
            onPreview={
              onMentionPreview
                ? () =>
                    onMentionPreview({
                      id,
                      text,
                      type
                    })
                : undefined
            }
            spellCheck={false}
          >
            {isValidElement(children) ? children : text}
          </StyledLink>
        ) : (
          <StyledButton
            {...restProps}
            ref={mentionRef}
            variant='link'
            onClick={handleClick}
            spellCheck={false}
          >
            {isValidElement(children) ? children : text}
          </StyledButton>
        )}
        {target && mentionContent}
      </>
    );
  }
);

export const MentionButtonConfig = {
  type: 'mention',
  xmlElement: 'pega-mention',
  regexPattern: /(?:^|)<pega-mention (?:[^\n/]|\/(?!>))+\/>/g,
  component: MentionButton,
  inject: ({ id, type, text, href }: { id: string; type: string; text: string; href?: string }) => {
    if (!id || !type || !text) return;

    const element = document.createElementNS(null, 'pega-mention');
    element.setAttribute('id', id);
    element.setAttribute('text', text);
    element.setAttribute('type', type);
    if (href) element.setAttribute('href', href);

    return serializer.serializeToString(element);
  },
  extract: (element: string) => {
    const attributes = getXMLAttributes(element);

    if (!attributes || !attributes.id || !attributes.text || !attributes.type) return undefined;
    return {
      id: attributes.id,
      text: attributes.text,
      type: attributes.type,
      href: attributes.href
    };
  }
};

export default MentionButton;
