import { FC, useState, useEffect, MutableRefObject, useRef, useContext, useMemo } from 'react';
import styled, { css } from 'styled-components';

import {
  Button,
  Card,
  CardContent,
  Flex,
  registerIcon,
  defaultThemeProp,
  ForwardProps,
  useI18n,
  Progress,
  usePrevious
} from '@pega/cosmos-react-core';
import { RichTextEditorState } from '@pega/cosmos-react-rte';
import * as arrowMicroDownIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/arrow-micro-down.icon';
import * as caretLeftIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/caret-left.icon';

import FeedAttachments from './FeedAttachments';
import FeedContent from './FeedContent';
import FeedReplyInput, { StyledReplyInputContainer } from './FeedReplyInput';
import { FeedContext, FeedReplyContext } from './Feed.context';
import { FeedPostProps } from './Feed.types';
import FeedContentHeader from './FeedContentHeader';
import FeedContentFooter from './FeedContentFooter';
import FeedEditRegion from './FeedEditRegion';
import { StyledFeedReplyContainer } from './FeedReply';

registerIcon(arrowMicroDownIcon, caretLeftIcon);

export const StyledFeedPost = styled(Card)(
  ({ theme }) => css`
    margin-block-start: calc(2 * ${theme.base.spacing});
  `
);

StyledFeedPost.defaultProps = defaultThemeProp;

const StyledPostContent = styled.div<{ showLines: boolean }>(({ showLines, theme }) => {
  return css`
    padding-inline-start: calc(6 * ${theme.base.spacing});

    ${showLines &&
    css`
      margin-inline-start: calc(2.5 * ${theme.base.spacing});
      border-inline-start: solid 0.0625rem ${theme.base.palette['border-line']};
      padding-inline-start: calc(3.5 * ${theme.base.spacing});
    `}
  `;
});

StyledPostContent.defaultProps = defaultThemeProp;

export const StyledReplies = styled.div(
  ({ theme }) => css`
    margin-inline-start: calc(-1.75 * ${theme.base.spacing});

    ${StyledFeedReplyContainer}, ${StyledReplyInputContainer} {
      padding-block-start: ${theme.base.spacing};
    }
  `
);

StyledReplies.defaultProps = defaultThemeProp;

const FeedPost: FC<ForwardProps & FeedPostProps> = (props: ForwardProps & FeedPostProps) => {
  const {
    id,
    avatarSrc,
    fullname,
    username,
    postTimestamp,
    content,
    postType = 'public',
    recipients,
    recipientsCount,
    attachments,
    liked,
    icon,
    postContext,
    userStatus,
    likes,
    likeCount,
    interactionsEnabled,
    maxContentHeight = Infinity,
    onMouseEnter,
    onMouseLeave,
    onCommentClick,
    onLikeClick,
    onLikeCountInteraction,
    likesLoading,
    onLoadMoreLikes,
    onRecipientInteraction,
    recipientsLoading,
    onLoadMoreRecipients,
    onUserClick,
    onShowMoreClick,
    newReplyField,
    menuItems,
    onEditSubmit,
    edited = false,
    editTimestamp,
    replies = [],
    replyInput,
    replyInputShown,
    replyCount,
    repliesLoading,
    onShowLessReplies,
    onShowMoreReplies,
    ...restProps
  } = props;
  const { announce } = useContext(FeedContext);
  const variant = 'post';

  const t = useI18n();
  const [editMode, setEditMode] = useState(false);
  const [emptyEditText, setEmptyEditText] = useState(false);
  const [replyInputOpen, setReplyInputOpen] = useState(false);
  const [replyInputRef, setReplyInputRef] = useState<MutableRefObject<
    RichTextEditorState | undefined
  > | null>(null);
  const prevReplies = usePrevious(replies);
  const suppressNextAnnouncement = useRef(false);

  useEffect(() => {
    if ((replyInputOpen || replyInputShown) && replyInputRef) {
      replyInputRef.current?.focus();
    }
  }, [replyInputOpen, replyInputShown, replyInputRef]);

  const replyCtxValue = useMemo(() => ({ onShowReplyInput: () => setReplyInputOpen(true) }), []);

  useEffect(() => {
    if (prevReplies) {
      const difference = prevReplies.length - replies.length;
      if (difference > 0 && !suppressNextAnnouncement.current) {
        const timeoutId = announce(t('feed_comment_removed'));
        return () => clearTimeout(timeoutId);
      }
      suppressNextAnnouncement.current = false;
    }
  }, [replies]);

  const hasReplies = !!replies?.length;

  return (
    <FeedReplyContext.Provider value={replyCtxValue}>
      <StyledFeedPost
        {...restProps}
        onMouseEnter={() => {
          onMouseEnter?.({ id });
        }}
        onMouseLeave={() => {
          onMouseLeave?.({ id });
        }}
      >
        <CardContent>
          <FeedContentHeader
            {...{
              variant,
              id,
              fullname,
              username,
              postTimestamp,
              content,
              avatarSrc,
              icon,
              userStatus,
              postType,
              postContext,
              recipients,
              recipientsCount,
              menuItems,
              edited,
              editTimestamp,
              recipientsLoading,
              onEditSubmit,
              onLoadMoreRecipients,
              onRecipientInteraction,
              onUserClick,
              setEditMode
            }}
          />
          <StyledPostContent showLines={hasReplies || replyInputOpen}>
            <Flex
              container={{ direction: 'column', pad: [0, editMode ? 0 : 2, 0, 0] }}
              item={{ grow: 1 }}
            >
              {editMode ? (
                <FeedEditRegion
                  {...{
                    variant,
                    id,
                    content,
                    setEditMode,
                    onEditSubmit,
                    emptyEditText,
                    setEmptyEditText
                  }}
                />
              ) : (
                <FeedContent maxContentHeight={maxContentHeight}>{content}</FeedContent>
              )}
              <FeedAttachments variant={variant} attachments={attachments} readOnly />
            </Flex>
            <FeedContentFooter
              {...{
                variant,
                id,
                username,
                liked,
                likes,
                likeCount,
                interactionsEnabled,
                likesLoading,
                replies,
                replyCount,
                repliesLoading,
                onCommentClick,
                onLikeClick,
                onLikeCountInteraction,
                onLoadMoreLikes,
                onShowMoreReplies,
                onShowLessReplies,
                replyInputOpen,
                setReplyInputOpen
              }}
            />

            <StyledReplies>
              {hasReplies && replies}
              {replyInputShown ||
                (replyInputOpen && (
                  <FeedReplyInput
                    onSetInputRef={({ ref }) => {
                      setReplyInputRef(ref);
                    }}
                    id={id}
                    attachments={replyInput.attachments}
                    onFilesAdded={replyInput.onFilesAdded}
                    onSubmit={e => {
                      setReplyInputOpen(false);
                      replyInput.onSubmit(e);
                    }}
                  />
                ))}
            </StyledReplies>
            {onShowLessReplies && !!replies.length && (
              <Flex container={{ justify: 'center', pad: [0, 0, 1], gap: 1 }}>
                <Button
                  variant='link'
                  onClick={() => {
                    onShowLessReplies({ id });
                    suppressNextAnnouncement.current = true;
                  }}
                >
                  {t('show_less')}
                </Button>
                {repliesLoading && <Progress placement='inline' />}
              </Flex>
            )}
          </StyledPostContent>
        </CardContent>
      </StyledFeedPost>
    </FeedReplyContext.Provider>
  );
};

export default FeedPost;
